.appForm {
  width: 100%;
  background-color: #12130f;
  padding: 25px 40px;
  overflow: auto;
  justify-content: center;
  align-items: center;
}



.formCenter {
  /* margin-bottom: 100px; */
  width: 100%;
  height: 100vh;
  background-color: #12130f;
  padding: 20vh 40px;
  overflow: auto;
  justify-content: center;
  align-items: center;
  align-content: center;
  justify-items: center;
  align-self: center;
  justify-self: center;


}

.formTitle {
  color: #707c8b;
  font-weight: 300;
  margin-bottom: 50px;


}

.formTitleLink {
  color: #707c8b;
  text-decoration: none;
  display: inline-block;
  font-size: 1.7em;
  margin: 0 10px;
  padding-bottom: 5px;
}

.formTitleLink:first-child {
  margin-left: 0;
}

.formTitleLink-active {
  color: white;
  border-bottom: 1px solid #6b5b95;
}

.formField {
  margin-bottom: 40px;
}

.formFieldLabel {
  display: block;
  text-transform: uppercase;
  font-size: 0.9em;
  color: white;
}
.formFieldLabel:hover {
  cursor: pointer;
}
.formFieldInput {
  width: 70%;
  background-color: transparent;
  border: none;
  color: white;
  outline: none;
  border-bottom: 1px solid #445366;
  font-size: 1em;
  font-weight: 300;
  padding-bottom: 10px;
  margin-top: 10px;
  text-align: center;
 
}

.formFieldInput::placeholder {
  color: #616e7f;
  text-align: center;
}

.formFieldButton {
  background-color: #401175;
  color: white;
  border: none;
  outline: none;
  border-radius: 25px;
  padding: 15px 70px;
  font-size: 0.8em;
  font-weight: 500;
}

.formFieldLink {
  color: #66707d;
  text-decoration: none;
  display: inline-block;
  border-bottom: 1.5px solid #6b5b95;
  padding-bottom: 5px;
}

.formFieldCheckboxLabel {
  color: #646f7d;
  font-size: 0.9em;
}

.formFieldCheckbox {
  position: relative;
  top: 1.5px;
}

.formFieldTermsLink {
  color: white;
  border-bottom: 1px solid #6b5b95;
  text-decoration: none;
  display: inline-block;
  padding-bottom: 2px;
  margin-left: 5px;
}

.socialMediaButtons {
  padding: 10px 100px 10px 0px;
}
